import {
	uploadHeadImg,
	getUserInfo,
	set_info,
	excute_set_info
} from '@/api/user.js'
export default{
	data(){
		return {
			currentH:uni.getSystemInfoSync().windowHeight,
			name:'',
			input:'',
			user:new Object()
		}
	},
	onLoad(options) {
		console.log(options);
		this.user=uni.getStorageSync("user");
		this.name=options.name;
		this.key=options.key;
		this.input=options.input;
	},
	methods:{
		submit(){
			try{
				excute_set_info({
					id:uni.getStorageSync("user").id,
					columnName:this.key,
					columnValue:this.input
				}).then(res=>{
					console.log(res);
					if(res.code==200){
						uni.showToast({
							title:res.msg,
							icon:'none'
						})
						uni.navigateBack({
							delta:1
						})
					}
				})
			}catch(e){
				//TODO handle the exception
			}
		}
	}
}
